  font-weight: bold;
  content: attr(data-drop-content);
  text-align: center;
  line-height: 24px;
  position: absolute;
  animation: droppable-pulsate 1s ease-in-out infinite alternate;
  top: -14px;
  left: -10px;
  height: 24px;
