/****************************************************************************************************************************
 * PAGE SETTINGS
 * Base styles / reset, full page layout
 ***************************************************************************************************************************/
body {
  background-color: #D2CBB1;
  margin: 0;
  overflow: hidden;
  /* otherwsie TinyMCE menues sometimes makes scrollbars show in main body */
}
input,
select,
textarea,
button {
  font-family: inherit;
  font-size: inherit;
  max-width: 100%;
}
* {
  user-select: none;
}
input,
textarea,
[contenteditable],
[contenteditable] *,
.evo-color span {
  user-select: text;
}
a[disabledhref] {
  color: #06C;
  color: -webkit-link;
  text-decoration: underline;
  cursor: auto;
}
a[disabledhref]:-moz-read-write {
  text-decoration: underline -moz-anchor-decoration;
  color: -moz-hyperlinktext;
}
/****************************************************************************************************************************
 * LAYOUT
 ***************************************************************************************************************************/
#page {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  overflow: hidden;
}
#main-edit-area {
  left: 0;
  right: 0;
  overflow: hidden;
}
#main-edit-area,
#toolbar {
  min-width: 600px;
}
#toolbar {
  z-index: 110 !important;
  background-color: #D2CBB1;
  box-sizing: border-box;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 40px;
}
#main-toolbox {
  backface-visibility: hidden;
  display: none;
  left: 0;
  width: 400px;
  box-shadow: 0 0 10px #808080, 0 0 5px #808080;
}
.withToolbox #main-toolbox {
  display: block;
}
.withToolbox #main-edit-area,
.withToolbox #toolbar {
  left: 400px;
}
#main-preview {
  backface-visibility: hidden;
  display: none;
  right: 0;
  width: 340px;
  overflow: hidden;
}
.withPreviewFrame #main-preview {
  display: block;
}
.withPreviewFrame #main-edit-area {
  right: 345px;
}
#main-edit-area:before,
#main-edit-area:after,
#frame-container:before,
#frame-container:after {
  content: "";
  display: block;
  position: absolute;
  border: 0;
  height: 40px;
  box-shadow: 0px 1px 10px #808080, 0px 0px 5px #808080;
  left: 0;
  right: 0;
  top: 0;
  z-index: 100;
}
#main-edit-area:after {
  top: auto;
  bottom: -40px;
}
#frame-container:before {
  top: -40px;
}
#frame-container:after {
  top: auto;
  bottom: -40px;
}
#main-wysiwyg-area {
  position: absolute;
  top: 40px;
  left: 0;
  right: 0;
  bottom: 0;
  overflow-y: auto;
  -webkit-transform: translate3d(0, 0, 0);
}
