 * GENERIC UTILITIES: 
 * DRAG&DROP, UPLOADER, TOASTER, TOOLTIP, FONTS, LOADER
 ***************************************************************************************************************************/
#main-toolbox .draggable.image {
  width: 85px;
  height: 85px;
  text-align: center;
  vertical-align: middle;
  padding: 5px;
  border: 0;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  background-origin: content-box;
}
#main-toolbox .draggable.image img {
  opacity: 0;
}
.ui-draggable-dragging {
  z-index: 10000;
}
.ui-draggable-dragging.sortable-placeholder {
  top: 0 !important;
  left: 0 !important;
}
.uploading .fileuploadtext {
  display: none;
}
.fileuploadtext {
  top: 0px !important;
  left: 0px !important;
  right: 0px !important;
  bottom: 0px !important;
  color: #000000;
}
.uploadzone .fileuploadtext {
  font-family: "trebuchet ms", arial, sans-serif;
  font-size: 16px;
  text-decoration: none !important;
  font-weight: bold;
}
.isdragging .uploadzone .fileuploadtext,
.isdraggingimg .uploadzone .fileuploadtext {
  display: none !important;
}
.uploadzone .progress {
  display: none;
}
.uploadzone.uploading .progress {
  display: block;
}
/* this is needed to push a dropzone over a "makeDroppable" (greater z-index)
   because IE10 doesn't support "pointer-events: none" and images drag/drop from desktop wouldn't work */
.mo-uploadzone {
  position: absolute;
  z-index: 15;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  display: block;
}
.toast-bottom-full-width {
  margin-left: 25%;
  margin-right: 25%;
  width: 50% !important;
}
body > .ui-tooltip {
  border-radius: 7px;
  border: 0px solid #807e7c;
  border-width: 1px \9;
  border-color: rgba(128, 126, 124, 0);
  box-shadow: 0 2px 20px #807e7c;
  color: #333332;
  background-color: #ffffff;
  padding: 0;
  overflow: hidden;
  font-family: "trebuchet ms", arial, sans-serif;
  font-size: 16px;
  text-decoration: none !important;
  font-size: 14px;
  border-width: 0px;
  padding: 8px;
  position: absolute;
  z-index: 9999;
  max-width: 300px;
  box-shadow: 0px 0px 5px #AAA;
}
.ui-helper-hidden-accessible {
  border: 0px none;
  clip: rect(1px, 1px, 1px, 1px);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0px;
  position: absolute !important;
  width: 1px;
}
i.mce-i-newsletter-profile:before {
  content: '\e826';
  font-family: "fontello";
}
@keyframes loading {
  0% {
    transform: scale(1, 1) rotate(0deg);
  }
  50% {
    transform: scale(1.5, 1.5) rotate(360deg);
  }
  100% {
    transform: scale(1, 1) rotate(720deg);
  }
}
.mo-standalone #loading.loading img {
  animation: loading 4s linear infinite;
}
@keyframes broken {
  0% {
    opacity: 0;
  }
  95% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
#loading.loading div {
  animation: broken 10s linear forwards;
  padding: 1em;
  opacity: 1;
}
#carousel-dialog .carousel-buttons {
